.Goods {
	display: block;
	width: 960px;
	margin: 0 auto;
	div {
		img {
			width: 960px;
			height: 330px;
			overflow: hidden; } } }


.GoodsGrid {
	display: flex;
	flex-wrap: wrap;
	flex-direction: row;
	margin-top: 50px;
	.pCard {
		position: relative;
		margin-bottom: 50px;
		max-height: 300px;
		margin-left: 40px;
		.buyCount {
			position: absolute;
			top: 10px;
			right: 15px;
			color: red; } } }



.goodImg {
	// width: 200px!important
	margin: 0 auto;
	display: block;
	height: 200px;
	object-fit: cover;
	max-width: 300px;
 }	// height: 200px!important

