.Menu {
 width: 700px;
 margin: 0 auto;
 .Wrapper {
  width: 700px;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  li {
   list-style-type: none;
   font-size: 14px;
   padding: 10px 20px;
   &:hover {}
   background: #f5f5f5; } } }

.FullMenu {
 left: 50%;
 transform: translateX(-50%);
 width: 80%;
 height: auto;
 min-height: 150px;
 top: 130px;
 background: #f5f5f5;
 position: fixed;
 z-index: 9999999; }


.Categories {
 display: flex;
 width: 100%; }

.Topics {
 display: flex;
 flex-direction: column!important;
 justify-content: flex-start;
 width: 20%;
 align-items: center;
 span {
  text-transform: uppercase;
  font-weight: 600; } }
.Sub {
 display: flex;
 flex-direction: column!important;
 list-style-type: none;
 text-align: left; }
