.CustomerDetails {
	width: 400px;
	h2 {
		font-size: 1.5rem;
		font-weight: 100!important;
		margin: 0 0 40px 0; }
	h4 {
		font-weight: 100!important;
		margin: 0; } }
.inputs {
	height: 400px;
	display: flex;
	flex-direction: column;
	justify-content: space-between;
	input {
		width: 100%; } }
.radioCard {
	display: flex;
	flex-direction: row;
	justify-content: space-between;
	width: 80%;
	i {
		display: block; } }

.shipping [class*="p-card-content"] {
	display: flex;
	direction: row;
	justify-content: space-between; }
