.header {
	height: 150px;
	width: 100%;
	background: white;
	display: flex;
	justify-content: center;
	flex-direction: column;
	position: fixed;
	top: 0px;
	z-index: 99999;
	align-items: center;
	border-bottom: 1px solid #ccc;
	.languages {
		min-width: 40px; }
	.cart {
		min-width: 225px;
		text-align: right; }
	.wrapper {
		width: 950px;
		display: flex;
		justify-content: space-between;
		margin: 0 auto; }



	.burgerIcon {
		img {
			width: 40px; } } }
